.shoe_store{
  
  height: 100vh;
  width: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;

}
.navBar{
 
  height: 15vh;
  width: 100%;
  background-color: #f4f4f2;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
 
  
}
.nav_link{
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  position: relative;
  margin: 0;
text-align: center;
padding: 40px ;
/* top: 20px; */
}
li{
  font-size: 20px;
}
.icon{
  position: absolute;
  left: 40px;

}
img{
  height: 100px;
  width: 100px;
}
.main_section{

  height: 85vh;
  width: 100%;
  display: flex ;
  background-color: #e0e0de
  
}
.main_section_div1{

  height: auto;
  width: 65%;
  padding: 40px;
  display: flex;
  gap: 10px;
      flex-wrap: wrap;
      justify-content: center;
}
.shoe_card{
  width: 40%;
  height: auto;
}
.shoe_card img{
  height: 120px;
  width: 60%;
}

.shoe_card_h5{
  margin: 0;
}
.shoe_card_p{
  margin: 0;
}
.main_section_div2{

height: auto;
width: 35%;
display: flex;
justify-content: center;
align-items: center;

}
.cart{
border: 1px solid #ccc;
padding: 20px;
min-width: 300px;
min-height: 200px;
border-radius: 5px;
box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
background-color: #f4f4f2;
    
}
.cart h5{
  margin: 0;
  font-size: 18px;
}
.cart_img img{
  height: 50px;
  width: 50px;
}